import React from 'react';
import { IonBackButton, IonButtons, IonHeader, IonPage, IonToolbar, IonTitle, IonContent } from '@ionic/react';
import { withRouter } from 'react-router';
import { Plugins, CameraResultType } from '@capacitor/core';


const StreamerPage: React.FC<any> = ({ match }) => {
    setTimeout(() => {Plugins.CameraPreview.start({
        parent: "content",
        toBack: true,
        position: "front"
    });}, 1000);
    return (
        <IonPage>
            <IonHeader>
                <IonToolbar>
                    <IonButtons slot="start">
                        <IonBackButton  />
                    </IonButtons>
                    <IonTitle>{match.params.channel}</IonTitle>
                </IonToolbar>
            </IonHeader>
            <IonContent fullscreen>
                <div id="content" ></div>
            </IonContent>
        </IonPage>
    );
};

export default withRouter(StreamerPage);
