import React from 'react';
import { IonBackButton, IonButtons, IonHeader, IonPage, IonToolbar, IonTitle, IonContent } from '@ionic/react';
import { withRouter } from 'react-router';

const LivePage: React.FC<any> = ({match}) => {
    console.log(match);
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonBackButton defaultHref="/" />
          </IonButtons>
          <IonTitle>{match.params.channel}</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent className="bg-class no-padding" >
        <iframe
            src={`https://player.twitch.tv/?channel=${match.params.channel}&parent=localhost&autoplay=true&controls=false&muted=false`}
            frameBorder="0"
            scrolling="no"
            allowFullScreen={true}
            height="100%"
            width="100%">
        </iframe>
      </IonContent>
    </IonPage>
  );
};

export default withRouter(LivePage);
