import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import {
  IonApp,
  IonIcon,
  IonLabel,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonTabs
} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { square, triangle, images } from 'ionicons/icons';
import EventPage from './pages/EventPage';
import Tab3 from './pages/Tab3';
import Details from './pages/Details';
import LivePage from './pages/LivePage';
import EventsListPage from './pages/EventsListPage';


/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';

/* Global CSS */
import './global.css';
import StreamerPage from './pages/StreamerPage';

const App: React.FC = () => (
  <IonApp>
    <IonReactRouter>
      <IonTabs>
        <IonRouterOutlet>
          <Route path="/" component={EventsListPage} exact={true} />
          <Route path="/tab1/:event" render={({match}) => <EventPage match={match} />} exact={true} />
          <Route path="/tab1/:event/live" component={StreamerPage} exact={true} />
          <Route path="/tab1/:event/live/:channel" render={({match}) => <LivePage match={match} />} exact={true} />
          <Route path="/tab3" component={Tab3} />
         
          {/* <Route path="/" render={() => <Redirect to="/tab1" />} exact={true} /> */}
        </IonRouterOutlet>
        <IonTabBar slot="bottom" >
          <IonTabButton tab="tab1" href="/">
            <IonIcon icon={triangle} />
            <IonLabel>Live</IonLabel>
          </IonTabButton>
          <IonTabButton tab="tab3" href="/tab3">
            <IonIcon icon={square} />
            <IonLabel>Options</IonLabel>
          </IonTabButton>
        </IonTabBar>
      </IonTabs>
    </IonReactRouter>
  </IonApp>
);

export default App;
